<template>
  <div id="app">
    <div class="nav">
      <el-button class="nav-item" type="primary" plain @click="clickToSQLView">SQL</el-button>
      <el-button class="nav-item" type="primary" plain @click="clickToHelloWorld">HelloWorld</el-button>
      <el-button class="nav-item" type="primary" plain @click="clickToAbout">About</el-button>
    </div>
    <router-view />
  </div>
</template>

<script>
export default {
  name: "App",
  methods: {
    clickToSQLView() {
      this.$router.push({ name: "SQLView" });
    },
    clickToHelloWorld() {
      this.$router.push({ name: "HelloWorld" });
    },
    clickToAbout() {
      this.$router.push({ name: "about" });
    },
  },
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
.nav {
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: blanchedalmond;
}
.nav-item {
  margin-left: 15px;
  background-color: white;
}
</style>



