<template>
  <div class="main">
    <MonacoEditor class="editor" v-model="originCode" language="javascript" theme="vs-dark" @contentChange="onContentChange" />
    <el-button type="primary" plain @click="onCopyOriginCode">复制原始代码</el-button>
    <el-button type="primary" plain @click="onCopyFormatCode">复制格式化代码</el-button>
    <MonacoEditor class="editor" v-model="formatCode" language="javascript" theme="vs-dark" />
  </div>
</template>

<script>
import MonacoEditor from "../components/MonacoEditor.vue";

export default {
  components: {
    MonacoEditor,
  },
  data() {
    return {
      originCode: "",
      formatCode: "",
    };
  },
  mounted() {
    this.originCode = window.sessionStorage.getItem("origin-code");
  },
  methods: {
    onContentChange(content) {
      window.sessionStorage.setItem("origin-code", content);
      this.formatCode = this.sqlFormat(content);
    },
    sqlFormat(sql) {
      return sql.replace(/[\r\n]/g, " ");
    },
    //#region - Action
    onCopyOriginCode() {
      navigator.clipboard
        .writeText(this.originCode)
        .then(() => {
          this.$message.success("原始代码已复制");
        })
        .catch((err) => {
          this.$message.error("复制发生异常");
        });
    },
    onCopyFormatCode() {
      // 使用 Clipboard API 将文本复制到剪贴板
      navigator.clipboard
        .writeText(this.formatCode)
        .then(() => {
          this.$message.success("格式化代码已复制");
        })
        .catch((err) => {
          this.$message.error("复制发生异常");
        });
    },
    //#endregion
  },
};
</script>

<style>
.main {
  width: 100%;
  height: 900px;
}
.editor {
  width: 100%;
  height: 200px;
  margin-bottom: 30px;
}
</style>



